.container_chevron {
  position: absolute;
  width: 24px;
  height: 24px;
  left: 50%;
  bottom: 70px;
  transform: translateX(-50%);
  animation: showchevron 4s ease-in;
  cursor: none;
  transition: 0.3s;
}
.container_chevron:hover {
  transform: translateX(-50%) scale(1.2);
}
.showonhover {
  color: transparent;
  transition: 0.3s;
}
.showonhover small {
  font-size: 10px;
}
.showonhover:hover {
  color: #fff;
}
@keyframes showchevron {
  0% {
    opacity: 0;
    transform: translate(-50%, 100px);
  }
  80% {
    opacity: 0;
    transform: translate(-50%, 100px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
}
.chevron {
  position: absolute;
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: " ";
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
}

.chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;
  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

