@media screen and (min-width: 320px) {
  .preloader__content {
    height: calc(
      1 *
        (
          var(--preloader-circle-min-size) * 1px +
            (
              var(--preloader-circle-max-size) -
                var(--preloader-circle-min-size)
            ) * ((100vw - 320px) / 2240)
        )
    );
  }
  .preloader__content {
    width: calc(
      1 *
        (
          var(--preloader-circle-min-size) * 1px +
            (
              var(--preloader-circle-max-size) -
                var(--preloader-circle-min-size)
            ) * ((100vw - 320px) / 2240)
        )
    );
  }
  .small-caps {
    font-size: calc(10% * 1px + (13 - 10) * ((100vw - 320px) / 2240));
  }
  .small-caps-2 {
    font-size: calc(15% * 1px + (13 - 15) * ((100vw - 320px) / 2240));
  }
  h2,
  .h2 {
    font-size: calc(
      var(--h2-min-font-size) * 1px +
        (var(--h2-max-font-size) - var(--h2-min-font-size)) *
        ((100vw - 320px) / 2240)
    );
  }
  .h2 {
    font-size: calc(
      var(--h2-min-font-size) * 1px +
        (var(--h2-max-font-size) - var(--h2-min-font-size)) *
        ((100vw - 320px) / 2240)
    );
  }
  h4,
  .h4 {
    font-size: calc(
      var(--h4-min-font-size) * 1px +
        (var(--h4-max-font-size) - var(--h4-min-font-size)) *
        ((100vw - 320px) / 2240)
    );
  }
  .preloader__content {
    height: calc(
      1 *
        (
          var(--preloader-circle-min-size) * 1px +
            (
              var(--preloader-circle-max-size) -
                var(--preloader-circle-min-size)
            ) * ((100vw - 320px) / 2240)
        )
    );
  }

  .preloader__content {
    width: calc(
      1 *
        (
          var(--preloader-circle-min-size) * 1px +
            (
              var(--preloader-circle-max-size) -
                var(--preloader-circle-min-size)
            ) * ((100vw - 320px) / 2240)
        )
    );
  }
  .xl {
    font-size: calc(
      var(--xl-min-font-size) * 1px +
        (var(--xl-max-font-size) - var(--xl-min-font-size)) *
        ((100vw - 320px) / 2240)
    );
  }
  .button {
    font-size: calc(10 * 1px + (13 - 10) * ((100vw - 320px) / 2240));
  }
}
