@keyframes preloader__hide {
  0% {
    opacity: 1;
    transform: blur(0px);
    backdrop-filter: blur(0px);
  }
  75% {
    opacity: 1;
    transform: blur(0px);
    backdrop-filter: blur(0px);
  }
  100% {
    transform: blur(10px);
    opacity: 0;
    backdrop-filter: blur(10px);
  }
}
@keyframes preloader__circle_hide {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}
@keyframes preloader__curtain_hide {
  0% {
    transform: translate(-50%, 60%) scaleY(0) scaleX(0);
  }
  40% {
    transform: translate(-50%, 60%) scaleY(0) scaleX(0);
  }

  75% {
    transform: translate(-50%, 40%) scaleY(0.85) scaleX(1);
  }
  100% {
    transform: translate(-50%, 40%) scaleY(1) scaleX(1);
  }
}

@keyframes preloader__header_hide {
  0% {
