  }
}
.split-text__line {
  display: block;
  text-align: center;
  position: relative;
  opacity: 1;
  visibility: inherit;
  overflow: hidden;
  min-width: 250px;
}
.split-text__char {
  position: relative;
  display: inline-block;
  opacity: 0;
  visibility: inherit;
  transform: translate(0%, 0%);
}
.loaded .split-text__char {
  transform: translate(0%, 0%);
  animation: split-text__char 3500ms 1;
  transition: 0.3s;
  opacity: 1;
}
.loaded .split-text__char:nth-child(1) {
  animation-duration: 3500ms;
}

.loaded .split-text__char:nth-child(2) {
  animation-duration: 3550ms;
}
.loaded .split-text__char:nth-child(3) {
  animation-duration: 3600ms;
}
.loaded .split-text__char:nth-child(4) {
  animation-duration: 3700ms;
}
.loaded .split-text__char:nth-child(5) {
  animation-duration: 3750ms;
}
.loaded .split-text__char:nth-child(6) {
  animation-duration: 3800ms;
}
@keyframes split-text__char {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }
  85% {
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
